terell (2000), dice que civilizaciones como la china, la india y la persa, desarrollaron culturas altamente sofisticadas y diversas, con ricas tradiciones filosóficas, religiosas y artísticas.")]),_c('div',{attrs:{"numero":"4","titulo":"América Precolombina"}},[_c('p',{staticClass:"mb-4"},[_vm._v("Antes de los europeos, América era un continente vibrante y diverso, habitado por cientos de pueblos indígenas con culturas únicas y adaptadas a sus respectivos entornos. Martínez (2002), expresa: \"tenemos a los mayas, aztecas y olmecas que son algunos de los pueblos indígenas más conocidos. Ellos desarrollaron avanzadas técnicas agrícolas, sistemas de escritura y calendarios complejos. Su arquitectura monumental, como las pirámides de Teotihuacán y Chichén Itzá, y las tradiciones religiosas y artísticas, reflejan una rica diversidad cultural\".")]),_c('p',{staticClass:"mb-0"},[_vm._v("Están también los incas, que crearon un vasto imperio con una red de caminos y una administración eficiente, la ingeniería avanzada, como las terrazas agrícolas y los sistemas de riego, y la cerámica y textiles elaborados, son testimonio de su sofisticación cultural. Martínez (2002), en su obra \"Los pueblos indígenas de América\", ofrece esta visión detallada de las culturas precolombinas y su desarrollo.")])]),_c('div',{attrs:{"numero":"5","titulo":"Colonialismo"}},[_vm._v("Se remonta a la expansión europea, a partir del siglo XV, la cual dio lugar a un proceso de colonización que tuvo un profundo impacto en la diversidad cultural, imponiendo lenguas, religiones y costumbres europeas, en muchas partes del mundo.")]),_c('div',{attrs:{"numero":"6","titulo":"Modernidad"}},[_vm._v("Llega con La Revolución Industrial y la globalización económica; estas aceleraron los procesos de intercambio cultural, pero también llevaron a una homogeneización de las culturas y a la pérdida de muchas tradiciones locales.")]),_c('div',{attrs:{"numero":"7","titulo":"Posmodernidad"}},[_vm._v("En las últimas décadas, ha surgido una mayor conciencia sobre la importancia de la diversidad cultural y se han desarrollado movimientos sociales que buscan proteger y promover las culturas minoritarias.")]),_c('div',{attrs:{"numero":"8","titulo":"Actualidad"}},[_vm._v("Entender la diversidad cultural y sus manifestaciones, resulta clave para diferentes contextos; en nuestro caso, en la formación pedagógica, nos proporciona herramientas fundamentales para la creación de entornos inclusivos, respetuosos y enriquecedores para todos los individuos, sin importar su origen cultural.")])])],1),_vm._m(3)]),_vm._m(4),_vm._m(5),_vm._m(6),_vm._m(7),_vm._m(8),_c('div',{staticClass:"bg-full-width bg-fondo-1 mb-0",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"p-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center mb-0"},[_c('div',{staticClass:"col-11 col-md-8 col-xl-7"},[_c('SlyderF',{attrs:{"columnas":"col-12 col-lg-6"}},[_c('div',{staticClass:"row py-4 px-3 h-100"},[_c('div',{staticClass:"col-sm-12 col-lg-12 shadow-app p-3 py-md-5 px-md-4 bg-color-white"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-6 col-md-4 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/t1/9.svg"),"alt":"Icono Wayuunaiki"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Wayuunaiki")]),_c('p',{staticClass:"text-center"},[_vm._v("Hablada por el pueblo Wayúu en La Guajira.")])])]),_c('div',{staticClass:"row py-4 px-3 h-100"},[_c('div',{staticClass:"col-sm-12 col-lg-12 shadow-app p-3 py-md-5 px-md-4 bg-color-white"},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-6 col-md-4 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/t1/10.svg"),"alt":"Icono Nasa Yuwe"}})])]),_c('h4',{staticClass:"text-center"},[_vm._v("Nasa Yuwe")]),_c('p',{staticClass:"text-center"},[_vm._v("Hablada por los Nasa en el Cauca.")])])]),_c('div',{staticClass:"row py-4 px-3 h-100"},[_c('div',{staticClass:"col-sm-12 col-lg-12 shadow-app p-3 py-md-5 px-md-4 bg-co