e transmiten de generación en generación y son una parte importante de la identidad cultural.")]),_c('div',{attrs:{"numero":"3","titulo":"Afrodescendiente"}},[_vm._v("Escalante (2005), sostiene que, la elaboración de artesanías y manualidades, como las máscaras y los tejidos, es una práctica común que refleja la herencia africana y la creatividad de las comunidades afrodescendientes; también la música y la danza, son elementos centrales de su cultura, pues los ritmos de tambor y los bailes tradicionales, como el mapalé, la cumbia y el currulao, son formas de preservar y transmitir la herencia africana. ")])])],1)]),_vm._m(14),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-0",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 d-none d-lg-block"},[_c('img',{attrs:{"src":require("@/assets/curso/t1/33.png"),"alt":"Imagen representativa de roles de género"}})]),_c('div',{staticClass:"col-sm-12 col-lg-7"},[_c('h5',[_vm._v("Roles de género")]),_c('p',[_vm._v("Estos roles varían significativamente. En algunas culturas, los roles son más rígidos y definidos, mientras que, en otras, hay una mayor flexibilidad y equidad de género. Por ejemplo, en muchas comunidades indígenas, las mujeres tienen roles importantes en la agricultura y la toma de decisiones comunitarias.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 d-none d-lg-block"},[_c('img',{attrs:{"src":require("@/assets/curso/t1/34.png"),"alt":"Imagen representativa de jerarquías sociales"}})]),_c('div',{staticClass:"col-sm-12 col-lg-7"},[_c('h5',[_vm._v("Jerarquías sociales")]),_c('p',[_vm._v("Estas pueden estar basadas en la edad, el género, la riqueza, el conocimiento o la posición, dentro de la comunidad. En algunas culturas, los ancianos son altamente respetados y tienen un papel central en la